
body,
* {
  margin: 0;
}
* + * {
  margin-top: 1.5em;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #222;
}
textarea {
  display: block;
  font-size: 1rem;
  width: calc(100% - 2rem);
  padding: 1em;
  margin-bottom: 0;
  -webkit-box-shadow: inset 0 0 1rem rgba(0,0,0,0.5);
          box-shadow: inset 0 0 1rem rgba(0,0,0,0.5);
  border-radius: 0;
}
section {
  margin-top: 0;
  padding-top: 1.5em;
  padding-bottom: 5em;
}
section p,
section h1,
section h2,
section h3,
section h4,
section h5,
section h6,
section div {
  max-width: 33rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}